<template>
  <div>
    <div class="container" style="max-height: 550px; overflow-y: scroll">
      <div v-for="product in data" :key="product.id" class="product-row row">
        <div class="col-md-4">
          <img
            :src="product.thumbnail"
            alt="Product 1"
            class="img-fluid product-image"
          />
        </div>
        <div class="col-md-8">
          <h3>{{ product.id }} - {{ product.title }}</h3>
          <p class="product-description">
            {{ product.description }}
          </p>
        </div>
      </div>

      <!-- Add more rows as needed -->
    </div>
    <div class="mt-3">
      <Paginate
        :totalPages="totalPages"
        @currentPage="changePage($event)"
        :color="selectedColor == 'black' ? 'dark' : selectedColor"
      ></Paginate>
    </div>
    <div>
      <div class="color-palette mt-3">
        <div
          v-for="(color, index) in colors"
          :key="index"
          :style="{ backgroundColor: color }"
          class="color-circle"
          :class="selectedColor == color ? 'selected' : ''"
          @click="selectedColor = color"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import vue3Pagin8 from "vue3-pagin8";
import axios from "axios";
export default {
  setup() {
    const loading = ref(false);
    const data = ref(null);
    const totalPages = ref(10);
    const pageLimit = ref(5);
    const colors = ref(["red", "green", "blue", "purple", "gray", "black"]);
    const selectedColor = ref("green");

    let getData = async () => {
      loading.value = true;
      try {
        let res = await axios.get(
          `https://dummyjson.com/products?limit=${pageLimit.value}`
        );
        data.value = res.data.products;
        totalPages.value = res.data.total / pageLimit.value;
      } catch (e) {
        console.error(e);
      }
    };
    let changePage = async (page) => {
      let res = await axios.get(
        `https://dummyjson.com/products?limit=${pageLimit.value}&skip=${
          (page - 1) * pageLimit.value
        }`
      );
      data.value = res.data.products;
      totalPages.value = res.data.total / pageLimit.value;
    };
    onMounted(getData);
    return {
      loading,
      data,
      totalPages,
      pageLimit,
      changePage,
      colors,
      selectedColor,
    };
  },
  components: {
    Paginate: vue3Pagin8,
  },
};
</script>

